<template>
    <div class="framework-content-wrapper">
        <current yiji="系统管理" erji="组织架构" class="test"></current>
        <div class="framework-content">
           <div class="content-left">
               <div class="content-left-title" v-show="companies.name"><img  height="21px" src="../../../../static/img/common/home_logo.png" /><span class="head_quarters" @click="getInfo('0')">{{companies.name}}</span></div>
               <ul class="list " v-show="companies" >
                   <!--<li class="guangzhou common L">广州分公司<span class="count">(111)</span></li>-->
                   <li class="shanghai common L" v-for="(company, $index) in companies.childrenList" :key="$index" @click.stop.prevent="collapse(company.id, $event, null)" :class="`L${company.id}`">{{company.name}}<span class="count"></span>
                        <ul class="common-list">
                            <!--<li class=" common dot">行政部<span class="count">(111)</span></li>-->
                            <li class="common dot X" v-for="(department, index) in company.childrenList" :key="index" @click.stop.prevent="collapse(department.id, $event, company.id)" :class="`X${department.id}`">{{department.name || '你好'}}<span class="count"></span>
                                <ul class="common-list-item">
                                    <li class="common dot J" v-for="childDepartment in department.childrenList" @click.stop.prevent="collapse(childDepartment.id, $event, company.id)" :class="`J${childDepartment.id}`">{{childDepartment.name}}<span class="count"></span></li>
                                    <!--<li class="common dot">一部<span class="count">(111)</span></li>-->
                                </ul>
                            </li>
                        </ul>
                   </li>
               </ul>
           </div>
            <div class="content-right">
                <div class="title">
                    <span class="text text_special" v-show="content">{{content.organName}}</span>
                    <div class="button-wrapper" style="display: flex">
                        <el-button class="del" @click="del(content.organNo)">删除</el-button>
                        <el-button type="primary" @click="handleAdd('edit_department', content.organNo)" class="toolBtn">编辑</el-button>
                    </div>
                </div>
                <div class="form1-wrapper">
                    <span>上级机构</span><input type="text" v-model="content.organParentName" disabled><br>
                    <span>主管</span><input type="text" v-model="content.organMgeName" disabled><br>
                    <span>类型</span><select value="" disabled>
                    <option value=""></option>
                    <option value="01" :selected="content.organType=='01'">总公司</option>
                    <option value="02" :selected="content.organType=='02'">分公司</option>
                    <option value="03" :selected="content.organType=='03'">办事处</option>
                    <option value="04" :selected="content.organType=='04'">部门</option></select><br>
                    <span>状态</span><select value="" disabled>
                    <option value=""></option>
                    <option value="1" :selected="content.status=='1'">启用</option>
                    <option value="0" :selected="content.status=='0'">停用</option></select>
                </div>
                <div class="title" style="margin-top: 36px;">
                    <span class="text">下级机构</span>
                    <el-button type="primary" @click="handleAdd('add_junior', content.organNo)" class="toolBtn">新增</el-button>
                </div>
                <div class="table-wrapper" style="margin-top: 13px; text-align: right;" v-show="tableData.list">
                    <template>
                        <el-table
                                :data="tableData.list"
                                border
                                stripe
                        >
                            <el-table-column
                                    prop="organChildName"
                                    label="名称"
                                    align="center"
                            >
                            </el-table-column>
                            <el-table-column
                                    label="类型"
                                    align="center">
                                <template scope="scope">
                                    <span>{{ scope.row.organType === '01' ? '总公司': scope.row.organType === '02' ? '分公司': scope.row.organType === '03' ? '办事处': '部门'}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="organMgeName"
                                    label="主管"
                                    align="center"
                            >
                            </el-table-column>
                            <el-table-column
                                    label="状态"
                                    align="center">
                                <template scope="scope">
                                    <span>{{ scope.row.status === '1' ? '启用': '停用' }}</span>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                    <div class="block" style="margin-top: 40px; display: inline-block;" v-show="tableData.total">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page.sync="tableData.pageNum"
                                :page-size="tableData.pageSize"
                                layout="total,prev, pager, next, jumper"
                                :total="tableData.total">
                        </el-pagination>
                    </div>
                </div>
                <div class="title" style="margin-top: 36px;">
                    <span class="text">机构人员</span>
                    <el-button type="primary" @click="handleAdd('add_person', content.organNo)" class="toolBtn">新增</el-button>
                </div>
                <div class="table-wrapper" style="margin-top: 13px; text-align: right;" v-show="tableData2.list">
                    <template>
                        <el-table
                                :data="tableData2.list"
                                border
                                stripe
                                max-height="400"
                        >
                            <el-table-column
                                    prop="userName"
                                    label="姓名"
                                    align="center"
                            >
                            </el-table-column>
                            <el-table-column
                                    prop="userNo"
                                    label="工号"
                                    align="center"
                            >
                            </el-table-column>
                            <el-table-column
                                    prop="remark"
                                    label="职位"
                                    align="center"
                                    width="170"
                            >
                            </el-table-column>
                            <el-table-column
                                    prop="mobileTEL"
                                    label="手机"
                                    align="center">
                            </el-table-column>
                            <el-table-column
                                    label="操作"
                                    align="center">
                                <template scope="scope">
                                    <i class="el-icon-delete" style="color: #FF9900"></i>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                    <div class="block" style="margin-top: 40px; display: inline-block;" v-show="tableData2.total">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange2"
                                :current-page.sync="tableData2.pageNum"
                                :page-size="tableData2.pageSize"
                                layout="total, prev, pager, next, jumper"
                                :total="tableData2.total">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
    import current from '../../common/current_position.vue'
    import $ from '../../../../static/bower_components/jquery/dist/jquery.min'
    export default {
        data() {
            return {
                content:'',
                show: false,
                companies: '',
                tableData: '',
                tableData2: ''
            }
        },
        created() {
           let self = this
            self.$axios.get('/iem_hrm/organ/queryOrganList/0')
              .then( res => {
                  self.companies = res.data.data[0]
                  self.$nextTick(function () {
                      var ulObj = $('.common-list')
                      let length = ulObj.length
                      for (let index=0;index<length;index++) {
                          let obj = ulObj[index]
                          if (index!=1) {
                              $(obj).slideUp(0)
                          }
                      }
                      $('.list>li:nth-child(2)').addClass('active')
                  })
              })
              .catch( res=> {
                  console.log('请求公司数据超时')
                  return false
              })
            self.$axios.get('/iem_hrm/organ/queryCurrentAndParentOrganDetail/01')
                .then( res => {
                    self.content = res.data.data
                })
                .catch( res=> {
                    console.log('请求公司详情数据超时')
                    return false
                })
            self.$axios.get('/iem_hrm/organ/queryChildOrganDetail/01')
                .then( res => {
                    self.tableData = res.data.data
                })
                .catch( res=> {
                    console.log('请求公司下级详情数据超时')
                    return false
                })
            self.$axios.get('/iem_hrm/organ/queryOrganMember/01')
                .then( res => {
                    self.tableData2 = res.data.data
                })
                .catch( res=> {
                    console.log('请求公司员工数据超时')
                    return false
                })
        },
//        watch: {
//            companies: '_reload',
//        },
        beforeMount() {
//            let self = this
//           setTimeout(function () {
//               self.content.title = $('.L0 .X0')[0].childNodes[0].nodeValue
//               $('.L0 .X0').addClass('active')
//               $('.L0').addClass('active')
//               self.content.super = $('.L0')[0].childNodes[0].nodeValue
//               self.tableData = []
//               var lenght = $('.L0 .X0 li').length
//               for (let i=0;i<lenght;i++) {
//                   var dep = $('.L0 .X0 li')[i].childNodes[0].nodeValue
//                   var obj = {
//                       dep: dep,
//                       name: '方清丽',
//                       state: '正常',
//                       type: '三级部门'
//                   }
//                   self.tableData.push(obj)
//               }
//           },200)
        },
        methods: {
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                let index = this.content.organNo
                let _self = this
                console.log(val)
                if (!_self.tableData.total) {
                    _self.$axios.get(`/iem_hrm/organ/queryChildOrganDetail/${index}`, {params:{pageNum: val}})
                        .then( res => {
                            _self.tableData = res.data.data
                            console.log(_self.tableData)
                            if (!_self.tableData.total) {
                                _self.tableData.total = 1
                            }
                        })
                        .catch( res=> {
                            console.log('请求公司下级详情数据超时222')
                        })
                }

            },
            handleCurrentChange2(val) {
                let index = this.content.organNo
                let _self = this
                _self.$axios.get(`/iem_hrm/organ/queryOrganMember/${index}`, {params:{pageNum: val}})
                    .then( res => {
                        _self.tableData2 = res.data.data
                        if (!_self.tableData2.total) {
                            _self.tableData2.pageNum = 0
                        }
                    })
                    .catch( res=> {
                        console.log('请求公司员工详情数据超时222')
                    })
            },
            handleAdd(tt, queryNo) {
//                this.$store.dispatch('change_frame', this.content)
                window.sessionStorage.setItem('frame_queryNo', queryNo)
                this.$router.push(tt);
            },
            getInfo(index) {
                const _self = this
                _self.$axios.get(`/iem_hrm/organ/queryCurrentAndParentOrganDetail/${index}`)
                    .then( res => {
                        _self.content = res.data.data
                    })
                    .catch( res=> {
                        console.log('请求公司详情数据超时')
                    })
                _self.$axios.get(`/iem_hrm/organ/queryChildOrganDetail/${index}`)
                    .then( res => {
                        _self.tableData = res.data.data
                    })
                    .catch( res=> {
                        console.log('请求公司下级详情数据超时')
                    })
                _self.$axios.get(`/iem_hrm/organ/queryOrganMember/${index}`)
                    .then( res => {
                        _self.tableData2 = res.data.data
                    })
                    .catch( res=> {
                        console.log('请求公司员工数据超时')
                    })
                $('.list').find('li').removeClass('active')
                $('.head_quarters').addClass('active')
            },
            collapse(index, e, parent) {
                let _self = this
                var reg = /L/
                var reg1 = /X/
                var reg2 = /J/
                if (reg.test(e.target.className)) {
                    _self.$axios.get(`/iem_hrm/organ/queryCurrentAndParentOrganDetail/${index}`)
                        .then( res => {
                            _self.content = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryChildOrganDetail/${index}`)
                        .then( res => {
                            _self.tableData = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司下级详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryOrganMember/${index}`)
                        .then( res => {
                            _self.tableData2 = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司员工数据超时')
                            return false
                        })
                    $('.L'+ index + ' >ul').slideToggle('slow')
                    $('.L'+ index).siblings().find('ul').slideUp('slow')
                    $('.list').find('li').removeClass('active')
                    $('.head_quarters').removeClass('active')
                    $('.L'+ index).addClass('active')
//                    $('.L'+ index).siblings().removeClass('active')
                }
                if (reg1.test(e.target.className) && parent!==null) {
//                    console.log($('.L'+ parent+' .X'+ index)[0].childNodes[0].nodeValue)
                    _self.$axios.get(`/iem_hrm/organ/queryCurrentAndParentOrganDetail/${index}`)
                        .then( res => {
                            _self.content = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryChildOrganDetail/${index}`)
                        .then( res => {
                            _self.tableData = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司下级详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryOrganMember/${index}`)
                        .then( res => {
                            _self.tableData2 = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司员工数据超时')
                            return false
                        })
//                    _self.content.title = $('.L'+ parent+' .X'+ index)[0].childNodes[0].nodeValue
                    $('.list').find('li').removeClass('active')
                    $('.head_quarters').removeClass('active')
                    $('.L'+ parent+' .X'+ index).addClass('active')
//                    $('.L'+ parent+' .X'+ index).parents('.L').addClass('active')
//                    $('.L'+ parent+' .X'+ index).siblings().removeClass('active')
//                    $('.L'+ parent+' .X'+ index).parents('.L').siblings().removeClass('active')
//                    $('.L'+ parent+' .X'+ index).parents('.L').siblings().find('.common-list>li').removeClass('active')
//                   var str = $('.L'+ parent+' .X'+ index).parents('.L' + parent)[0].childNodes[0].nodeValue
//                    _self.content.super = str
//                   var lenght = $('.L'+ parent+' .X'+ index + ' >ul>li').length
//                    for (let i=0;i<lenght;i++) {
//                       var dep = $('.L'+ parent+' .X'+ index + ' >ul>li')[i].childNodes[0].nodeValue
//                       var obj = {
//                           dep: dep,
//                           name: '方清丽',
//                           state: '正常',
//                           type: '三级部门'
//                       }
//                        _self.tableData.push(obj)
//                    }
                    $('.L'+ parent+' .X'+ index + ' >ul').slideToggle('slow')
                    $('.L'+ parent+' .X'+ index).siblings().find('ul').slideUp('slow')
                }
                if (reg2.test(e.target.className) && parent!==null) {
                    _self.$axios.get(`/iem_hrm/organ/queryCurrentAndParentOrganDetail/${index}`)
                        .then(res => {
                            _self.content = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryChildOrganDetail/${index}`)
                        .then( res => {
                            _self.tableData = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司下级详情数据超时')
                            return false
                        })
                    _self.$axios.get(`/iem_hrm/organ/queryOrganMember/${index}`)
                        .then( res => {
                            _self.tableData2 = res.data.data
                        })
                        .catch( res=> {
                            console.log('请求公司员工数据超时')
                            return false
                        })
                    $('.list').find('li').removeClass('active')
                    $('.head_quarters').removeClass('active')
                    $('.L'+ parent+' .J'+ index).addClass('active')
                }
            },
            _reload() {
                console.log('reload')
                this.$axios.get('/iem_hrm/organ/queryOrganList/0')
                    .then( res => {
                        console.log('reload.axios')
                        console.log(res.data.data[0])
                        return res.data.data[0]
                    })
                    .catch( res=> {
                        console.log('请求公司数据超时')
                        return false
                    })
            },
            del(index) {
                const self = this
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    self.$axios.delete(`/iem_hrm/organ/deleteOrganInfo/${index}`)
                        .then(res => {
                            if (res.data.retMsg !== "操作成功") {
                                this.$message({
                                    type: 'error',
                                    message: res.data.retMsg
                                });
                            } else {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                                setTimeout(function () {
                                    window.location.reload(true)
                                },3000)
                            }
                        })
                        .catch(res=>{
                            console.log('删除失败')
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
        components: {
            current,
        }
    }
</script>

<style scoped>
.framework-content-wrapper *{
    margin:0;
    padding: 0;
}
.framework-content-wrapper .test{
   padding-left: 10px;
}
.framework-content-wrapper{
    padding:29px 0 16px 20px ;
}
.framework-content-wrapper .framework-content{
    margin-top: 29px;
    display: flex;
}
.framework-content .content-left{
    flex: 0 0 350px;
    width: 350px;
    background: #fff;
    padding: 32px 0 0 32px;
}
.framework-content .content-right{
    flex: 1;
    padding-left: 41px;
    padding-right: 40px;
    padding-bottom: 40px;
    background: #fff;
    border-left: 1px solid #eee;
}
.content-left-title>img{
    vertical-align: middle;
    margin-right: -54px;
}
.content-left .head_quarters{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    line-height: 16px;
    height: 16px;
    display: inline-block;
    margin-left: 6.7px;
    vertical-align: middle;
    cursor: pointer;
}
.content-left .list{
    margin-top: 32px;
    padding-left: 18px;
}
.content-left .common{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #333333;
    letter-spacing: 0;
    line-height: 14px;
    margin-bottom: 20px;
    margin-right: 4px;
    cursor: pointer;
}
.content-left ul>li:first-child{
    margin-top: 20px;
}
.list .count{
    margin-left: 4px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #CCCCCC;
    letter-spacing: 0;
}
ul, li{
    list-style: none;
}
.list>.L:before{
    display: inline-block;
    content: 'L';
    color: #CCCCCC;
    width: 8px;
    height: 12px;
    margin-right: 10px;
}
.list .dot:before{
    display: inline-block;
    content: '';
    background: #CCCCCC;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    margin-right: 10px;
    vertical-align: middle;
}
.list .common-list{
    padding-left: 18px;
}
.list .common-list .common-list-item{
    padding-left: 16px;
}
.content-right .title{
    border-bottom: 1px solid #EEEEEE;
    display: flex;
    align-items: center;
    height: 80px;
    justify-content: space-between;
}
.content-right .title .text{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    /*margin-right: 309px;*/
    border-bottom: 2px solid #363D47;
    height: 80px;
    line-height: 80px;
    width: 80px;
}
.content-right .title .text_special{
    width: auto;
}
.content-right .title .del {
    border-radius: 0;
    height: 40px;
    line-height: 40px;
    width: 120px;
    color: #FF9900;
    border-color:#FF9900 ;
    margin-right: 20px;
}
.content-right .title .toolBtn {
    border-radius: 0;
    height: 40px;
    line-height: 40px;
    width: 120px;
    background: #FF9900;
    border: none;
}
.form1-wrapper{
    padding-left: 10px;
    margin-top: 40px;
}
.form1-wrapper>span{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: 0;
    line-height: 14px;
    width: 56px;
    height: 14px;
    display: inline-block;
    margin-right: 30px;
    vertical-align: middle;
    text-align: right;
}
.form1-wrapper>input, .form1-wrapper>select{
    background: #FFFFFF;
    border: 1px solid #EEEEEE;
    border-radius: 4px;
    width: 300px;
    height: 40px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #333333;
    letter-spacing: 0;
    line-height: 14px;
    text-indent: 1em;
    margin-bottom: 20px;
}
.active{
    color: orange !important;
}
/*.fade-enter-active, .fade-leave-active {*/
    /*transition: all .5s*/
/*}*/
/*.fade-enter, .fade-leave-to {*/
    /*opacity: 0;*/
    /*height: 0;*/
/*}*/

</style>



